.contactContainer {
    display: flex;
    flex-direction: column;
    width: 30dvw;

    h1 {
        font-size: 3em;
    }

    .textAreaCounter {
        display: flex;
        align-items: end;
    }

    div {
        display: flex;
        flex-direction: column;
        gap: 20px;

        input {
            color: lime;
            background: transparent;
            border: 2px lime solid;
            border-radius: 5px;
            caret-shape: underscore;
            font-size: 1.2em;
            text-shadow: 0 0 10px lime;
            filter: blur(0.5px);
        }
        
        textarea {
            color: lime;
            background: transparent;
            border: 2px lime solid;
            border-radius: 5px;
            caret-shape: underscore;
            font-size: 1.2em;
            filter: blur(0.5px);
        }

        label {
            margin-top: 10px;
            font-size: 1.2em;
        }
    }
}