.textContainer {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 40px;

    h2 {
        font-size: 72px;
        text-transform: uppercase;
        filter: blur(1px);
    }

    h1 {
        font-weight: normal;
        font-size: 88px;
        filter: blur(1px);
    }

    div {
        display: flex;
        flex-direction: row;
        gap: 20px;

        button {
            font-size: 1em;
        }
    }
}

