.windowWrapper {
    display: flex;
    width: unset;
    position: absolute;
    color: lime;

    .windowContainer {
        border: 2px lime solid;
        backdrop-filter: blur(10px) brightness(80%);
        position: relative;
        border-radius: 5px;


        img {
            border-radius: 5px;
        }

        .windowTitle {
            border: 1px lime solid;
            padding: 10px;
            cursor: grab;
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 20px;

            .windowButtons {
                display: flex;
                gap: 10px;
                justify-content: end;

                .windowButton {
                    transition: .5s;
                    border-radius: 5px;
                    margin: 2px;
                    cursor: pointer;
                }

                .windowButton:hover {
                    background: rgb(44, 44, 44)
                }
            }
        }
    }

    .file {
        transition: .25s;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 10px;
        width: 10em;
        color: lime;

        .fileName {
            white-space: nowrap;
        }
    }

    .file:hover {
        scale: 1.05;
    }
    
    .file:active {
        scale: 0.9;
        color: green;
        text-shadow: 0 0 10px green;
    }
}
