.container {
    height: calc(100dvh - 100px);
    overflow: hidden;
    position: relative;
    top: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: lime;

    .wrapper {
      max-width: 60dvw;
      width: 60dvw;
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      justify-content: space-evenly;
      align-items: center;
      margin: auto;
      height: 80%;
      // border: 1px red solid;
    }
  }
  